var React = require('react');

var SVGSet = React.createClass({displayName: "SVGSet",
  render: function() {
    return (
      React.createElement("svg", {className: "svg", style: this.props.style, viewBox: "0 0 300 42.2"}, 
        this.props.children
      )
    );
  }
});

module.exports = SVGSet;
